.App-header {
  /* text-align: center; */
  background-color:#1498d5;
  color: white;
}
.App-logo{
  line-break: auto;
}
.App * {
  -webkit-user-select: none !important;
     -moz-user-select: none !important;
      -ms-user-select: none !important;
          user-select: none !important;  
}
.App a{
 color: #ededed;
 font-weight: bold;
}

.App .PanelContainer{
  position: absolute;
  width: 100%;
  height: 100%;
  right: 0%;
  bottom: 0%;
  left: 60%;
  top: 50%;
}

.AppPanel{
  border-radius: 10px;
  background: #999999;
  opacity: 0.8; 
  max-width: 40%;
  max-height: 50%;
  z-index: 200;
  display: block
}
.AppPanel:after {
  content: "";
  display: table; 
  clear: both;  
}


.AppPanel img{
  max-width: 50px;
}



